.shop {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}
.shop > .sidebar {
    min-width:200px ;
    height: 100%;
    /* background-color: crimson; */
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}
.shop > .main {
    min-width:400px ;
    height: 100%;
    /* background-color: forestgreen; */
    flex: 4;
}
