.shopping-basket{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    overflow: hidden;
    gap: 10px;

}
.shopping-basket > .basket-title{
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    gap: 10px;
    font-family: monospace;
}
.shopping-basket > .basket-title > .icon{
    font-size: 22px;

}
.shopping-basket > .basket-product-list{
    width: 100%;
    flex: 8;
    overflow-y: scroll;
    padding: 10px 15px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 10px;


}
.shopping-basket > .basket-product-list::-webkit-scrollbar{
    display: none;
}
.shopping-basket > .checkout{
    flex: 3;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
/* checkout section */
.shopping-basket > .checkout > .total-price{
    background-color: whitesmoke;
    width: max-content;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-family: monospace;
    padding: 5px 15px;
    border-radius: 10px;
    gap: 10px;
    text-transform: uppercase;
    font-size: 14px;
    border: 1px solid rgba(0, 0, 0, 0.4);

}
.shopping-basket > .checkout > .total-price > .price{
    font-size: 18px;

}
.shopping-basket > .checkout > .payment{
    width: 160px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;    
}
.shopping-basket > .checkout > .payment > .checkout-button{
    background-color: forestgreen;
    color: white;
    width: 150px;
    height: 50px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
    text-transform: uppercase;
    font-family: monospace;
    font-size: 16px;
    letter-spacing: 1px;
    user-select: none;
    transition: 0.2s all;
}
.shopping-basket > .checkout > .payment > .checkout-button:hover{
    cursor: pointer;
    filter: brightness(95%);

}
