.product-card-in-basket {
    background: whitesmoke;
    width: 100%;
    height: 100px;
    margin-top: 10px;
    border-radius: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: black;
    border: 1px solid rgba(0, 0, 0, 0.1);
    gap: 10px;
    overflow: hidden;
}
/* Thumbnails */
.product-card-in-basket > .thumbnails {
    height: 100px;
    width: 100px;
    background: #000;
    overflow: hidden;
    padding: 15px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product-card-in-basket > .thumbnails > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    user-select: none;
    -webkit-user-drag: none;
}
/* product detauls */
.product-card-in-basket > .details {
    width: 180px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
    font-size: 14px;
    font-weight: normal;
    text-transform: capitalize;
}
/* counter */
.product-card-in-basket > .count {
    /* background: #000; */
    width: 50px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product-card-in-basket > .count > input[type="number"] {
    width: 100%;
    height: 40px;
    text-align: center;
    font-size: 18px;
    border: none;
    outline: none;
    border-radius: 10px;
    font-family: inherit;
    border: 1px solid rgba(0, 0, 0, 0.2);
}
.product-card-in-basket > .count > input[type="number"]:focus {
    border: 1px solid rgba(0, 0, 0, 0.4);
}
/* Remove Button */
.product-card-in-basket > .remove-button{
    width: 40px;
    height: 100%;
    color: crimson;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s all;
    font-size: 18px;

    
}
.product-card-in-basket > .remove-button:hover{
    background-color: crimson;
    color: white;
    cursor: pointer;
    
}

