.product-list{
    width: 100%;
    height: 100%;
    /* background: #000; */
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 20px;
    gap: 20px;
    overflow-y: scroll;
}
.product-list::-webkit-scrollbar{
    display: none;
}